*{
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.board{
  width: calc(64px * 8);
  height: calc(64px * 8);
  display: flex;
  flex-wrap: wrap;
}

.cell{
  width: 64px;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.cell img{
  height: 48px;
  width: 48px;
  position: relative;
}

.white{
  background-color: #ad7a54d4;
}
.black{
  background-color: #f8e0bdc4;
}

.selected {
  background-color: brown;
}

.available{
  height: 12px;
  width: 12px;
  border-radius: 50%;
  background-color: darkgreen;
}

.lost{
  height: calc(50vh - 60px);
  padding: 30px;
  margin-left: 50px;
  background-color: lightgrey;
}

.timer{
  padding: 30px;
  margin-left: 20px;
}

.timer button{
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 5px;
  /*background-color: #c1cfdc;*/
  border-color: lightgray;
  font-size: 16px;
}